.is-style-btn-primary .wp-block-button__link, .btn-outline, .btn-white-border, .btn-primary {
  display: inline-block;
  padding: 12px 24px 7px;
  font-weight: bold;
  font-size: 18px;
  color: #FFFFFF;
  line-height: 1.2;
  text-align: center;
  text-decoration: none;
  border-width: 2px;
  border-style: solid;
  transition: 0.2s ease-in-out;
  border-radius: 8px;
}

[role=button],
[type=button] {
  -webkit-appearance: none;
  outline: 0;
}

.btn-primary {
  background-color: #1D0056;
  border-color: #1D0056;
}
.btn-primary:active, .btn-primary:focus, .btn-primary:hover {
  color: #1D0056;
  background-color: #FFFFFF;
}
.btn-white-border {
  background-color: #1D0056;
  border-color: #FFFFFF;
}
.btn-white-border:active, .btn-white-border:focus, .btn-white-border:hover {
  color: #FFFFFF;
}
.btn-outline {
  color: #1D0056;
  text-transform: uppercase;
  border-color: #1D0056;
  background-color: transparent;
}
.btn-outline:active, .btn-outline:focus, .btn-outline:hover {
  color: #FFFFFF;
  background-color: #1D0056;
}

.wp-block-button.is-style-outline > .wp-block-button__link.has-light-white-background-color {
  padding-bottom: 7px;
}

.is-style-btn-primary .wp-block-button__link {
  background-color: #1D0056;
  border-color: #1D0056;
}
.is-style-btn-primary .wp-block-button__link:active, .is-style-btn-primary .wp-block-button__link:focus, .is-style-btn-primary .wp-block-button__link:hover {
  color: #1D0056;
  background-color: #FFFFFF;
}

.zantimes-newsletter {
  position: relative;
  display: flex;
  min-height: 58vh;
  background-size: cover;
  background-position: 50% 90%;
  background-repeat: no-repeat;
}
.zantimes-newsletter__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: end;
  margin-top: auto;
  height: 100%;
  padding-bottom: 80px;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
}
.zantimes-newsletter__heading {
  color: #FFFFFF;
}
.zantimes-newsletter__form {
  position: relative;
  max-width: 680px;
}
.zantimes-newsletter__form #mc_embed_signup {
  background: transparent;
}
.zantimes-newsletter__form #mc_embed_signup form {
  margin-top: 0;
  margin-left: 0;
}
.zantimes-newsletter__form #mc_embed_signup .mc-field-group {
  padding-bottom: 0;
  width: 100%;
  min-height: auto;
  position: relative;
}
.zantimes-newsletter__form #mc_embed_signup .mc-field-group input {
  padding: 18px 15px 12px;
  outline: 0;
  border-radius: 10px 0x 0 10px;
  border: none;
  width: 99%;
}
.zantimes-newsletter__form #mc_embed_signup .mc-field-group input::placeholder {
  color: #000000;
}
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe {
  position: absolute;
  top: 0;
  right: 0;
  display: inline-block;
  margin: 0;
  padding: 18px 30px 15px;
  height: 62.4px;
  border-radius: 0 10px 10px 0;
  background-color: #1D0056;
  color: #FFFFFF;
  font-family: "Cambay", sans-serif;
  font-weight: 700;
  font-size: inherit;
}
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe-form div.mce_inline_error {
  margin: 12px 0 0 0;
  padding: 12px 15px 8px;
  font-size: 15px;
  background-color: #FFFFFF;
}
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe-form input.mce_inline_error {
  border: 0;
}
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe-form #mce-responses {
  display: block;
  margin: 0;
  padding: 0;
  width: auto;
}
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe-form #mce-error-response {
  color: #e85c41;
  background-color: #FFFFFF;
}
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe-form #mce-error-response,
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe-form #mce-success-response {
  margin: 12px 0 0;
  padding: 12px 15px 8px;
  width: auto;
  font-size: 15px;
  font-weight: 700;
  border-radius: 3px;
}
.zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe-form #mce-success-response {
  color: #FFFFFF;
  background-color: #1D0056;
}
.zantimes-newsletter__form #mc_embed_signup .indicates-required,
.zantimes-newsletter__form #mc_embed_signup label,
.zantimes-newsletter__form #mc_embed_signup .refferal_badge {
  display: none !important;
}
@media screen and (max-width: 767px) {
  .zantimes-newsletter__wrapper {
    padding-bottom: 50px;
  }
  .zantimes-newsletter__form #mc_embed_signup {
    width: 100%;
  }
  .zantimes-newsletter__form #mc_embed_signup form {
    margin-right: 0;
  }
  .zantimes-newsletter__form #mc_embed_signup .foot {
    display: block;
    width: 100%;
  }
  .zantimes-newsletter__form #mc_embed_signup #mc-embedded-subscribe {
    width: 100%;
    display: block;
    position: unset;
    margin-top: 10px;
    border-radius: 0;
  }
}